body {
  font-family: sans-serif;
  color: var(--five-color);
}

#second_container {
  background-color: var(--four-color);
  display: flex;
  height: 100%;
  width: 90%;
  right: 0px;
  position: absolute;
  /* padding-right: 10%; */
}

#main_container_dashboard {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  height: 100%;
  width: 100%;
}

:root {
  --primary--color: #171a21;
  --secondary--color: #66c0f4;
  --third--color: #1b2838;
  --four-color: #2a475e;
  --five-color: #c7d5e0;
}

#right_navbar_maincontainer {
  background-color: var(--third--color);
  display: flex;
  flex-direction: column;
  position: absolute;
  justify-content: space-between;
  height: 100%;
  width: 10%;
}
#right_navbar_header > img {
  height: 100%;
  width: 100%;
}
#right_navbar_header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 5%;
}

#right_navbar_button_features {
  /* background-color: blue; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 50%;
  padding-top: 20%;
}

#right_buttons {
  /* background-color: yellow; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 80%;
}
button:hover {
  color: var(--secondary--color);
}
button {
  color: var(--five-color);
  border: 0px;
  background-color: var(--third-color);
  cursor: pointer;
}
#right_tags_container {
  height: 100%;
  /* background-color: orange; */
  justify-content: space-between;
  align-items: center;
  display: flex;
  flex-direction: column;
}

#tags_container {
  /* background-color: green; */
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  height: 60%;
  /* padding-left: 10%; */
}

#right_footer_contaienr {
  position: relative;
  font-size: 13px;
  background-color: var(--third-color);
  display: flex;
  flex-direction: column;
  /* height: 100%; */
  align-items: center;
  width: 100%;
  height: 20%;
  padding-bottom: 10%;
  /* position: absolute; */
  /* bottom: 0px; */
}
.right_footer:hover {
  color: var(--secondary--color);
}
.right_footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* position: absolute; */
  padding-top: 10%;
  cursor: pointer;
}

#header_second_navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  /* height: 100%; */
  height: 20%;
  position: absolute;
  /* top: 0%; */
}
#second_navbar_button > button {
  font-size: 15px;
}
#second_navbar_button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 3%;
  position: absolute;
  top: 10%;
}

#second_navbar_search_login_section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 25%;
  position: absolute;
  right: 0%;
  padding-right: 5%;
  top: 2%;
}

/* #search_input {
  width: 80%;
} */

#logoAcount {
  display: flex;
  justify-content: center;
  gap: 10px;
  align-items: center;
  height: 100%;
  width: 100%;
}

#search_input {
  width: 90%;
  height: 51%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 5%;
  border-radius: 9px;
  background-color: var(--four-color);
  color: var(--secondary--color);
  border-color: var(--five-color);
  /* box-shadow: turquoise;
   */
  border-style: solid;
}

#search_input::placeholder {
  color: var(--five-color);
}

#logoAcount > img {
  width: 10%;
  height: 50%;
  border-radius: 50%;
  position: relative;
}

#arrow_btn {
  cursor: pointer;
}
#userName:hover,
#arrow_btn:hover {
  color: var(--secondary--color);
}

#sort_bar_second_container {
  display: flex;
  width: 100%;
  height: 20%;
  justify-content: space-between;
  align-items: center;
  padding-left: 3%;
  padding-right: 15%;
}

#sort_nav_contaienr {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
